.listContact {
  list-style: disc;
  margin-top: 10px;
}
.listItem {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.text {
  font-weight: 500;
}
.buttonDelete {
  width: 100px;
  cursor: pointer;
  text-align: center;
  border: 1px solid black;
  border-radius: 5px;
  font-weight: 700;
  color: white;
  padding: 5px;
  background-color: #2a363b;
}
.buttonDelete:hover {
  border: 1px solid white;
}
